@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+KR&display=swap);
:root {
  --contBox-border-radius: 3px;
  --contBox-border: 1px solid #f0f4f5;
  --white: #f0f4f5;
  --bgDeep: #000000;
  --bgLow: #1f222a;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  text-decoration: none;
  color: #f0f4f5;
  color: var(--white);
  font-family: "Noto Sans KR", sans-serif;
}

body {
  background: #000000;
  background: var(--bgDeep);
  overflow-y: scroll;
}

body .displayNone {
  display: none;
}
body .displayBlock {
  display: block;
}
body .displayFlex {
  display: flex;
}
.absolute {
  position: absolute;
}
.imgWidth {
  display: block;
  width: 100%;
}
.equipMainImg {
  top: 0;
  transform: scale(0.9);
}
.textCenter {
  text-align: center;
}

.height100vh {
  height: 100vh;
  overflow: hidden;
}
.vw07 {
  font-size: 0.7vw;
}
.rem07 {
  font-size: 0.7rem;
}
.rem08 {
  font-size: 0.8rem;
}
.rem09 {
  font-size: 0.9rem;
}
.rem1 {
  font-size: 1rem;
}
.rem11 {
  font-size: 1.1rem;
}
.rem12 {
  font-size: 1.2rem;
}
.rem13 {
  font-size: 1.3rem;
}
.rem17 {
  font-size: 1.7rem;
}
.zIndex10 {
  z-index: 10;
}
.zIndex11 {
  z-index: 11;
}
.zIndex99 {
