.innerApp {
  padding : .5rem;
}
.loadingBg {
  width : 100%;
  position : absolute;
  top : 0;
  min-height : 100vh;
  background : rgba(0,0,0,.7);
}
.AppWrap {
  max-width : 600px;
  min-width : 310px;
  margin : 0 auto;
  background : var(--bgLow);
  border-radius : var(--contBox-border-radius);
  padding : .5rem;
}


/* home section 공통 */
.homeSection {
  margin-bottom : 20px;
}
.homeSectionTitle {
  margin-bottom : 10px;
  background : var(--bgDeep);
  padding : .3rem;
  border-radius : 3px 3px 0 0;
}
/* header */
.header {
  width : 100%;
  margin-bottom : 1.5rem;
}
.innerHeader {
  max-width : 600px;
  margin : 0 auto;
  padding : .5rem;
}
.logo {
  background : url("https://github.com/sangmin802/loa-hands/blob/master/public/img/all.png?raw=true");
  background-position : 403px 3.5px;
  width : 160px;
  margin : 0 auto 10px;
  height : 50px;
  cursor : pointer;
}
.logo * {
  display : inline-block; width : 100%; height : 100%;
}
.searchForm {
  display : flex;
  margin : 0 auto;
  width : 60%;
  height : -webkit-fit-content;
  height : -moz-fit-content;
  height : fit-content;
}
input[type=text] {
  width : 80%;
  background : none;
  border : 0;
  border-bottom : var(--contBox-border);
  padding : .3rem;
}
input:focus {
  outline : none;
  background : none;
  border : 0;
  border-bottom : var(--contBox-border);
}
input[type=submit] {
  width : 20%;
  background : none;
  border : 0;
  border : var(--contBox-border);
  font-weight : bold;
  border-radius : 3px 3px 3px 0;
  padding : .3rem;
  cursor : pointer;
}
.event {
  width : 49%;
  border : var(--contBox-border);
  border-radius : var(--contBox-border-radius);
  overflow : hidden;
  margin-bottom : 2%;
  cursor : pointer;
}
.eventContent {
  padding : .3rem;
  background : var(--bgDeep);
}
.eventsWrap {
  display : flex;
  justify-content : space-between;
  flex-wrap : wrap;
}
.timer {
  width : 32%;
  width : calc((100% - 4%) / 3 - 0.01px);
  margin-right : 2%;
  margin-bottom : 2%;
  border : var(--contBox-border);
  border-radius : var(--contBox-border-radius);
  overflow : hidden;
  position : relative;
}
.timer:nth-child(3n){
  margin-right : 0;
}
.endTimeBg {
  position : absolute;
  z-index : 1;
  width : 100%;
  height : 100%;
  display : none;
  background : rgba(0,0,0,.5);
}
.timerName {
  background : var(--bgDeep);
  padding : .1rem;
}
.timerContent {
  display : flex;
  justify-content : space-between;
  align-items : center;
  padding : .3rem;
}
.timerImg {
  width : 25%;
  position : relative;
}
.timerLv {
  position : absolute;
  bottom : 0;
  right : 0;
  background : rgba(0,0,0,.5);
  padding : .1rem;
  border-radius : var(--contBox-border-radius);
}
.timerTime {
  width : 70%;
  display : flex;
  flex-direction : column;
  justify-content : center;
  align-items: center;
}
.timeOut {
  color : #FF9933;
}
.timerWrap {
  display : flex;
  flex-wrap : wrap;
}
.calendar0 {
  margin-bottom: 10px;
}
.calendarTitle {
  background: var(--bgDeep);
  font-size: 0.9rem;
  margin-bottom: 5px;
  padding: 0.3rem;
  border-radius: var(--contBox-border-radius);
}
.calendar .timerWrap {
  display: flex;
  justify-content: space-between;
}
.calendar .timer {
  flex-grow: 1;
}
.calendar0 .timer:last-child,
.calendar1 .timer:last-child {
  margin-right: 0;
}

.userInfo {
  border-radius: var(--contBox-border-radius);
}
/* userInfoTop */
.userInfoTop {
  position: relative;
  width: 100%;
  background: var(--bgDeep);
  border-radius: var(--contBox-border-radius);
  padding: 1rem 0.5rem 1rem;
}

/* userInfoBottom */
.userInfoBottom {
  margin-top: 1rem;
}
.tabContentWrap {
  background: var(--bgDeep);
  padding: 0.5rem;
  border-radius: 0 0 5px 5px;
}

.tabContent1 .columnList > .imgTextWrap .textWrap > div {
  font-size: 0.7rem;
}
.tabContent1 .columnList > .imgTextWrap .textWrap > div:last-child {
  font-size: 0.9rem;
}

.showExpeditionWrap {
  float : right;
  background : var(--bgLow);
  padding : .3rem;
  border-radius : var(--contBox-border-radius);
  cursor : pointer;
  border : var(--contBox-border);
}
.basicInfo {
  display : flex;
  width : 100%;
  margin-top : .5rem;
}
.basicInfo * {
  font-size : .9rem;
}
.basicInfo .textWrap {
  flex-direction : row;
  justify-content : left;
  align-items : center;
  width : calc((100% - .5rem) / 2 - .1px);
}
.basicInfo .textWrap > div:first-child {
  margin-right : .3rem;
  border-radius : var(--contBox-border-radius);
  border : var(--contBox-border);
  background : var(--bgLow);
  padding : .3rem .5rem;
  overflow : visible;
  overflow : initial;
}

/* 클래스만 */
.basicInfo:first-child > div:last-child .imgWrap {
  border : var(--contBox-border);
  border-radius : 50%;
}
.basicInfo:first-child > div:last-child .imgWrap img {
  border : 0;
}
.basicInfo:first-child > div:first-child {
  width : -webkit-fit-content;
  width : -moz-fit-content;
  width : fit-content;
}
.basicInfo:first-child > .imgTextWrap .textWrap div {
  border : 0;
  background : transparent none repeat 0 0 / auto auto padding-box border-box scroll;
  background : initial;
  padding : 0;
}

/* 컬렉션 */
.userInfoCollection {
  display : flex;
  margin-top : 15px;
}
.imgTextWrap {
  display : flex;
  align-items : center;
  margin-bottom : .3rem;
}
.imgTextWrap .textWrap {
  width : calc(88% - .5rem -0.1px);
  min-width : calc(100% - .5rem - 40.1px);
}
.imgTextWrap .imgWrap {
  width: 12%;
  min-width: 40px;
  margin-right: .5rem;
}
.imgTextWrap:last-child {
  margin-bottom : 0;
}
.imgWrap {
  position: relative;
}

.textWrap {
  display : flex;
  /* width : calc(88% - .5rem -0.1px);
  min-width : calc(100% - .5rem - 40.1px); */
  flex-direction : column;
  justify-content : center;
